	module.exports = {"data":{"markdownRemark":{"html":"<p>Gatsby supports <a href=\"https://facebook.github.io/react/docs/react-dom.html#browser-support\">the same browsers as the current stable version of React.js</a> which is currently IE9+ as well as the most recent versions of other popular browsers.</p>\n<h2 id=\"polyfills\"><a href=\"#polyfills\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Polyfills</h2>\n<p>Gatsby uses the ES6 Promise API. As some older browsers don’t support this, Gatsby includes by default a Promise polyfill. If you would like to provide your own Promise polyfill, you can set <code>polyfill</code> to <code>false</code> in your <code>gatsby-config.js</code>:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>module.exports = {\n  polyfill: false,\n  // ...\n}</code></pre>\n      </div>\n<h2 id=\"specify-what-browsers-your-project-supports-using-browserslist\"><a href=\"#specify-what-browsers-your-project-supports-using-browserslist\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Specify what browsers your project supports using “Browserslist”</h2>\n<p>You may customize your list of supported browser versions by declaring a <a href=\"https://github.com/ai/browserslist\"><code>\"browserslist\"</code></a> key within your <code>package.json</code>. Changing these values will modify your JavaScript (via <a href=\"https://github.com/babel/babel-preset-env#targetsbrowsers\"><code>babel-preset-env</code></a>) and your CSS (via <a href=\"https://github.com/postcss/autoprefixer\"><code>autoprefixer</code></a>) output.</p>\n<p>This article is a good introduction to the growing community of tools around Browserslist — <a href=\"https://css-tricks.com/browserlist-good-idea/\">https://css-tricks.com/browserlist-good-idea/</a></p>\n<p>By default, Gatsby emulates the following config:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>javascript\n// package.json\n{\n  &quot;browserslist&quot;: [\n    &quot;&gt; 1%&quot;,\n    &quot;IE &gt;= 9&quot;,\n    &quot;last 2 versions&quot;\n  ]\n}</code></pre>\n      </div>","excerpt":"Gatsby supports  the same browsers as the current stable version of React.js  which is currently IE9+ as well as the most recent versions of…","timeToRead":1,"frontmatter":{"title":"Browser Support"},"parent":{"__typename":"File","relativePath":"docs/browser-support.md"}}},"pathContext":{"slug":"/docs/browser-support/"}}

